<template>

<div >

    
    <h3>Live Video Stream (receiving)</h3>
   
      <video mute='true' playsinline autoplay id='v' width='50%' height='50%'></video>


    <div id='controls' class='hidden'>
      <div>Someone is here! Press Play.</div>
      <button
        id='play'
        style=''>
          Play
      </button>
    </div>

    <div id='wait'>
      Waiting for someone to connect.
    </div>

     <p class="timer">
              timer: {{timer}} <br /> 
     </p>

    <div class="lineChart"> 
         <div class="small">
            <line-chart :chart-data="datacollection"></line-chart>
         </div>
    </div>
</div>
  
</template>

<script src="../../node_modules/osc/dist/osc-browser.min.js"></script>
<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.3.1/jquery.min.js"></script>

            
<script>
import osc from "osc";
import $ from 'jquery';
import LineChart from '../components/LineChart.js';


 /// websocket OSC Data receiving and sending 
  var port = new osc.WebSocketPort({
          url: "wss://circusfamilyprojects.nl:8084" //ws://localhost:8083 online server wss://circusfamilyprojects.nl:8084
        }); 

 port.open();


export default {
  

  data() {
  return{ 
      timer: '',
      valueOne: 40,
      valueTwo: 80,
      valueThree: 30,
      valueFour: 70,
      valueFive: 50,
      valueSix: 30,
     datacollection: null
    
  }},
  components: {
      LineChart
    },
   created: function(){
             this.OSCMessage();      
          //console.log(message);

     },
   mounted: function(){

       
        this.fillData()
       /// websocket WebRTC for live stream

    (async function() {
  const config = {
    iceServers: [{
      urls: ['stun:stun.l.google.com:19302']
    }]
  };

  window.play.addEventListener('click', () => {
    try {
      window.v.play();
      window.controls.classList.add('hidden');
    } catch (e) {
      console.error(e);
    }
  });

  const getRandomId = () => {
    return Math.floor(Math.random() * 10000);
  };

  const peerId = getRandomId();
  const peerType = 'screen';
  const connections = new Map();

  let ws;
  const getSocket = async (peerId, peerType) => {
    if (ws) return ws;

    return new Promise((resolve, reject) => {
      try {
        const protocol = (
          window.location.protocol === 'https:' ?
            'wss:' :
            'ws:'
        );
        ws = new WebSocket('wss://circusfamilyprojects.nl:4084');  // ws://localhost:4083 online server wss://circusfamilyprojects.nl:8084

        const onOpen = () => {
          ws.send(JSON.stringify({
            type: 'register',
            peerType,
            peerId,
          }));

          ws.removeEventListener('open', onOpen);
          resolve(ws);
        };

        ws.addEventListener('open', onOpen);
      } catch (e) {
        reject(e);
      }
    });
  };

  try {
    console.log('in screen');
    const socket = await getSocket(peerId, peerType);
    socket.addEventListener('message', async (e) => {
      try {
        const msg = JSON.parse(e.data);
        console.log('msg', msg);

        if (msg.type === 'offer') {
          const peerConnection = new RTCPeerConnection(config);
          connections.set(msg.from, peerConnection);

          peerConnection.ontrack = (e) => {
            console.log('on track', e);
            window.v.srcObject = e.streams[0];
            window.wait.classList.add('hidden');
            window.controls.classList.remove('hidden');
          };

          peerConnection.onicecandidate = (e) => {
            if (e.candidate) {
              socket.send(JSON.stringify({
                type: 'candidate',
                from: peerId,
                to: msg.from,
                data: e.candidate,
              }));
            }
          };

          await peerConnection.setRemoteDescription(msg.data);
          const sdp = await peerConnection.createAnswer();
          await peerConnection.setLocalDescription(sdp);

          console.log('sending answer');
          socket.send(JSON.stringify({
            to: msg.from,
            from: peerId,
            type: 'answer',
            data: peerConnection.localDescription
          }));
        }

        if (msg.type === 'disconnect') {
          const connection = connections.get(msg.from);
          if (connection) {
            console.log('Disconnecting from', msg.from);
            connection.ontrack = null;
            connection.onicecandidate = null;
            connection.close();
            connections.delete(msg.from);
          }
        }

        if (msg.type === 'candidate') {
          const connection = connections.get(msg.from);
          if (connection) {
            console.log('Adding candidate to', msg.from);
            connection.addIceCandidate(new RTCIceCandidate(
              msg.data
            ));
          }
        }
      } catch (e) {
        console.error(e);
      }
    });

  } catch (e) {
    console.error(e);
  }
})();


     },
  computed: {


  },
  methods:{
       OSCMessage: function(){ 
               
        port.on("message", (oscMessage) => {
          
             //console.log(oscMessage);
             this.OSCMessages(oscMessage);
          
      });
      },
       OSCMessages: function(oscMessage){ 

        if(oscMessage.address == '/timer'){
          this.timer= oscMessage.args[0]
         
        }
         //console.log(oscMessage); 
         if(oscMessage.address == '/chart'){
          this.valueOne = oscMessage.args[0]
          this.valueTwo = oscMessage.args[1]
          this.valueThree = oscMessage.args[2]
          this.valueFour = oscMessage.args[3]
          this.valueFive = oscMessage.args[4]
          this.valueSix = oscMessage.args[5]
          //console.log(this.valueOne); 
          this.fillData ();
        }
        

      //console.log(oscMessage); 
          
     },
      fillData () {
        this.datacollection = {
          labels: [ 
            "Cas",
            "Taco",
            "Wout",
            "Dominique",
            "Lotte",
            "Peter plantenbak",
            ],
          datasets: [
            {
              label: 'Data One',
              backgroundColor: '#3a4445',
              data: [this.valueOne, this.valueTwo, this.valueThree, this.valueFour, this.valueFive, this.valueSix,],
              pointBackgroundColor: '#38ff9c'
            },
          ]
        }
      },
  },
  
}


</script>

