  display: inline-block;
  margin: 0 10px;
}

.wrapper{
  padding-top: 20px;
}

.btn-primary{
    margin-top: 20px;
}

.color {
  margin: auto;
  width: 50%;
  width: 80px;
  background-color: rgba(71, 255, 178, 0.699);
  height: 80px;
  
}
.timer{
     font-size: 50px;
}

.lineChart{
      margin: auto;
    width: 50%;
    max-width: 600px;
    padding-bottom: 50px;
}

</style>